export default{
	
	login:function(){
		//
		$('#login-tunp a').on('click',function(){
			var i = $(this).index();
			$(this).addClass('login-active').siblings().removeClass('login-active');
			switch (i){
				case 0: {
					$('#login').css('display','block');
					$('#register').css('display','none');
					
				};
					break;
				case 1: {
					$('#login').css('display','none');
					$('#register').css('display','block');
				}
					break;
			}
		})
		
		//
		$('#login-login').on('click',function(){
			if($('#username').val().toString().trim()===''){
				$('#username-judge').html('');
				return false;
			}else{
				$('#username-judge').html('');
			}
			if($('#password').val().toString().trim()==''){
				$('#password-judge').html('');
				return false;
			}else{
				$('#password-judge').html('');
			}
			return false;
		})
		
		
		//
		$('#register-register').on('click',function(){
			var phon = /^1[34578]\d{9}$/;
			if($('#usn').val().toString().trim()===''){
				$('#usnJudge').html('');
				return false;
			}else{
				$('#usnJudge').html('');
			}
			if($('#pwd1').val().toString().trim()===''){
				$('#pwd1Judge').html('');
				return false;
			}else{
				$('#pwd1Judge').html('');
			}
			if($('#pwd2').val().toString().trim()===''){
				$('#pwd2Judge').html('');
				return false;
			}else{
				$('#pwd2Judge').html('');
			}
			
			if($('#pwd1').val() !==$('#pwd2').val()){
				$('#pwd2Judge').html('');
				return false;
			}
			if($('#phone').val().toString().trim()===''){
				$('#phoneJudge').html('');
				return false;
			}else{
				$('#phoneJudge').html('');
			}
			
			
			if(phon.test($('#phone').val().toString().trim())==false){
				$('#phoneJudge').html('');
				return false;
			}else{
				$('#phoneJudge').html('');
			}
			
			
			if($('#verificationCode').val().toString().trim()===''){
				alert('')
				$('#verificationCode').html('');
				return false;
			}
			if($('#checkbox').prop('checked')!==true){
				alert('');
				return false;
			}
			alert('chenggong ')
			return false;
		})
		
		
		
	}
	
	
	
}


